@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&family=Poppins:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'icomoon';
  src:  url(/social_network/static/media/icomoon.e7b81250.eot);
  src:  url(/social_network/static/media/icomoon.e7b81250.eot#iefix) format('embedded-opentype'),
    url(/social_network/static/media/icomoon.f5e0ee66.ttf) format('truetype'),
    url(/social_network/static/media/icomoon.8c10f541.woff) format('woff'),
    url(/social_network/static/media/icomoon.112b7eba.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-settings:before {
  content: "\e90a";
}
.icon-contacts:before {
  content: "\e909";
}
.icon-edit:before {
  content: "\e90b";
}
.icon-send:before {
  content: "\e905";
}
.icon-paper-clip .path1:before {
  content: "\e906";
  color: rgb(207, 216, 220);
}
.icon-paper-clip .path2:before {
  content: "\e907";
  margin-left: -1em;
  color: rgb(0, 0, 0);
}
.icon-smile:before {
  content: "\e908";
}
.icon-search:before {
  content: "\e904";
}
.icon-friends:before {
  content: "\e900";
}
.icon-user:before {
  content: "\e901";
}
.icon-message:before {
  content: "\e902";
}
.icon-newspaper:before {
  content: "\e903";
}

/*Reset*/
* {
  padding: 0;
  margin: 0;
  line-height: 1;
  box-sizing: border-box;
}
a, img, button, input, textarea {
  display: block;
}
textarea {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  resize: none;
}
a, img, button {
  cursor: pointer;
}
button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
a {
  text-decoration: none;
}
button:focus, input:focus {
  outline: none;
}
ul {
  list-style-type: none;
}

html {
  font-size: 10px;
}

.App {
  width: 100%;
  max-height: 100vh;
  height: 100vh;
  position: relative;
}
.app__wapper {
  width: 100%;
  height: calc(100% - 8rem);
  display: flex;
  justify-content: space-between;
  background-color: #F1F3F6;
}

.header {
  position: relative;
  width: 100%;
  height: 8rem;
  padding: 0 1.5rem;
  background: #fff;
  box-shadow: 0px 0px 20px 0px rgba(44, 101, 144, 0.1);
  z-index: 2;
