.user__block {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.user__block .user__info__block {
  display: flex;
  align-items: center;
}

.user__block .user__info__block .user__icon {
  width: 4rem;
  height: 4rem;
  margin-right: 1.5rem;
}

.user__block .user__info__block .user__icon img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.user__block .user__name {
